'use strict';

// MODULES //

import isReadOnly from '@stdlib/ndarray-base-assert-is-read-only';
import getDType from '@stdlib/ndarray-base-dtype';
import getShape from '@stdlib/ndarray-base-shape';
import getStrides from '@stdlib/ndarray-base-strides';
import getOffset from '@stdlib/ndarray-base-offset';
import getOrder from '@stdlib/ndarray-base-order';
import getData from '@stdlib/ndarray-base-data-buffer';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Expands the shape of an array by inserting a new dimension of size one at a specified axis.
*
* ## Notes
*
* -   A provided axis must reside on the interval `[-N-1, N]`, where `N` is the rank (i.e., number of dimensions) of the provided input array. If provided a negative `axis`, the axis position at which to insert a singleton dimension is computed as `N + axis + 1`. Hence, if provided `-1`, the resolved axis position is `N` (i.e., a singleton dimension is appended to the input array).
*
* @param {ndarray} x - input array
* @param {integer} axis - axis at which to insert a singleton dimension
* @throws {RangeError} must provide a valid axis
* @returns {ndarray} output array
*
* @example
* import array from '@stdlib/ndarray-array';
*
* var x = array( [ [ 1, 2 ], [ 3, 4 ] ] );
* // returns <ndarray>
*
* var shx = x.shape;
* // returns [ 2, 2 ]
*
* var y = expandDimensions( x, 1 );
* // returns <ndarray>
*
* var shy = y.shape;
* // returns [ 2, 1, 2 ]
*
* var v = y.get( 0, 0, 0 );
* // returns 1
*
* v = y.get( 0, 0, 1 );
* // returns 2
*
* v = y.get( 1, 0, 0 );
* // returns 3
*
* v = y.get( 1, 0, 1 );
* // returns 4
*/
function expandDimensions( x, axis ) {
	var strides;
	var shape;
	var ord;
	var sh;
	var st;
	var N;
	var i;

	sh = getShape( x, false );
	st = getStrides( x, false );
	ord = getOrder( x );
	N = sh.length;

	strides = [];
	shape = [];

	if ( axis < 0 ) {
		if ( axis < -N-1 ) {
			throw new RangeError( format( '0if5H,L6', N, N, axis ) );
		}
		axis += N + 1;
	} else if ( axis > N ) {
		throw new RangeError( format( '0if5H,L6', N, N, axis ) );
	}
	if ( axis === 0 ) {
		// Prepend singleton dimension...
		shape.push( 1 );
		strides.push( st[ 0 ] );

		// Copy remaining dimensions...
		for ( i = 0; i < N; i++ ) {
			shape.push( sh[ i ] );
			strides.push( st[ i ] );
		}
	} else if ( axis === N ) {
		// Copy dimensions...
		for ( i = 0; i < N; i++ ) {
			shape.push( sh[ i ] );
			strides.push( st[ i ] );
		}
		// Append singleton dimension...
		shape.push( 1 );
		strides.push( st[ N-1 ] );
	} else {
		// Insert a singleton dimension...
		for ( i = 0; i < N+1; i++ ) {
			if ( i === axis ) {
				shape.push( 1 );
				if ( ord === 'row-major' ) {
					strides.push( st[ i-1 ] );
				} else { // ord === 'column-major'
					strides.push( st[ i ] );
				}
			} else if ( i < axis ) {
				shape.push( sh[ i ] );
				strides.push( st[ i ] );
			} else { // i > axis
				shape.push( sh[ i-1 ] );
				strides.push( st[ i-1 ] );
			}
		}
	}
	if ( isReadOnly( x ) ) {
		// If provided a read-only view, the returned array should also be read-only...
		return new x.constructor( getDType( x ), getData( x ), shape, strides, getOffset( x ), ord, { // eslint-disable-line max-len
			'readonly': true
		});
	}
	return new x.constructor( getDType( x ), getData( x ), shape, strides, getOffset( x ), ord ); // eslint-disable-line max-len
}


// EXPORTS //

export default expandDimensions;
